<template>
	<div class="SelectOrder">
		<header><i class="iconfont icon-xiangzuo1" @click="toReceipt"></i><span>选择订单</span></header>
		<div class="container">
			<table cellspacing="0">
				<thead>
					<tr>
						<td>选择</td>
						<td  @click="SortArr('Receipt_SelectOrderData','receiptKey','SortCondition1',$event)">收货单号</td>
						<td  @click="SortArr('Receipt_SelectOrderData','storerKey','SortCondition2',$event)">货主</td>
						<td  @click="SortArr('Receipt_SelectOrderData','externalreceiptkey2','SortCondition3',$event)">客户单号</td>
						<td  @click="SortArr('Receipt_SelectOrderData','qtytotal','SortCondition4',$event)">总数量</td>
						<td  @click="SortArr('Receipt_SelectOrderData','qtyreceived','SortCondition5',$event)">已收数量</td>
						<td  @click="SortArr('Receipt_SelectOrderData','skucnt','SortCondition6',$event)">sku品类</td>
					</tr>
				</thead>
				<tbody>
					<tr v-for='(dataItem,index) in Receipt_SelectOrderData'  :id="index"  :key='index'  @click="selectData(index)">
						<td class="iconfont"  :class="{ 'icon-xuanzhong':index==detail_item,'icon-weixuanzhong':index!=detail_item}"></td>
						<td>{{dataItem.receiptKey}}</td>
						<td>{{dataItem.storerKey}}</td>
						<td>{{dataItem.externalreceiptkey2}}</td>
						<td>{{dataItem.qtytotal}}</td>
						<td>{{dataItem.qtyreceived}}</td>
						<td>{{dataItem.skucnt}}</td>
					</tr>
				</tbody>
			</table>
			<div class="kong"></div>
			<p class="positionFixed"><span class="g_singleBtn" @click="selectData_submit">确定</span></p>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"SelectOrder",
		data(){
			return{

				Receipt_SelectOrderData:[],//列表数据
				detail_item:0,//默认为第一个
				SortCondition1:false,
				SortCondition2:false,
				SortCondition3:false,
				SortCondition4:false,
				SortCondition5:false,
				SortCondition6:false,
			}
		},
		mounted(){
			$("thead").find("td").eq(0).siblings().append('<i class="iconfont"></i>')
		},
		created(){

			console.log(this.$store.state.Receipt_SelectOrderData)

			var Receipt_SelectOrderData = this.$store.state.Receipt_SelectOrderData;

			this.Receipt_SelectOrderData = Receipt_SelectOrderData;
		},
		methods:{
			// 返回上一页 录入单号 页面
			toReceipt:function(){

				var that = this;

				this.$router.push({
					name:"Receipt",
					query:{
						ReceiptKey:that.$route.query.ReceiptKey,
						ExternalReceiptKey2:that.$route.query.ExternalReceiptKey2
					}
				})
			},
			//点击选择具体订单
			selectData:function(idx){

				this.detail_item = idx;

				
			},
			// 点击确认按钮 跳转到录入单号页面
			selectData_submit:function(){


				var receiptKey = this.Receipt_SelectOrderData[this.detail_item].receiptKey//收货单号
				var externalreceiptkey2 =  this.Receipt_SelectOrderData[this.detail_item].externalreceiptkey2//客户单号

				this.$router.push({
					name:"Receipt",
					query:{
						ReceiptKey:receiptKey,
						ExternalReceiptKey2:externalreceiptkey2
					}
				})
			}
		}
	}
</script>
<style lang="less" scoped>
	.SelectOrder{
		&>.container{
			width:100%;
			padding-bottom: 130/50rem;
			table{
				tr{
					td{
						min-width: 160/50rem;
					}
					td:nth-child(1),td:nth-child(3){
						min-width: 110/50rem;
					}
					td:nth-child(5){
						min-width: 130/50rem;
					}
				}
			}
			.iconfont{
				font-size: 40/50rem;
			}
		}
	}
	@media screen and (max-width: 332px) and (min-width: 251px){
		.SelectOrder{
			&>.container{
				table{
					tr{
						td{
							min-width: 200/50rem;
						}
						td:nth-child(1),td:nth-child(3){
							min-width: 130/50rem;
						}
						td:nth-child(5){
							min-width: 170/50rem;
						}
					}
				}
				.iconfont{
					font-size: 50/50rem;
				}
			}
		}
	}

	@media screen and (max-width: 250px){
		.SelectOrder{
			&>.container{
				table{
					tr{
						td{
							min-width: 260/50rem;
						}
						td:nth-child(1),td:nth-child(3){
							min-width: 160/50rem;
						}
						td:nth-child(5){
							min-width: 210/50rem;
						}
					}
				}
				.iconfont{
					font-size: 50/50rem;
				}
			}
		}
		
	}
</style>


